*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.outbox{
  width:100%;
  height:100vh;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  background: lightblue;
 flex-direction: row;
}
.box{
  width:25%;
  height:80vh;
  background: white;
  border-radius: 20px;
  box-shadow: 5px 5px 25px -5px black;
  overflow:auto;
}
.list{
  display: flex; 
  justify-content: start;
  align-items: center;
  flex-direction: row;
}
.list .fa-close{
  width:25px;
  height:25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: gray;
  margin-right: 10px;
  margin-left: 5px;
  border-radius: 50%;
  border: none;
  outline: none;
  color: #fff;
  transition: all 0.2s ease 0s;
  box-shadow: 5px 5px 25px -5px black;
}
.list .fa-close:hover{
  box-shadow: 0px 5px 10px rgb(233, 59, 59);
  color: #ffffff;
  transform: translateY(-4px);
  background-color: crimson;
}
h1{
  color: white;
  background: transparent;
  background-color: purple;
  margin-bottom:15px;
  margin-top: 15px;
  padding:6px 0px 2px 0px;
  box-shadow:5px 5px 15px -5px black;
}
input{
  outline: none;
  border: none;
  border-bottom: 3px solid rgb(242, 18, 197);
  text-align: center;
  font-size:30px;
  font-weight: 500;
  background: transparent;
  height: 30px;
  width: 65%;
  margin-bottom: 20px;
}
.btn{
  font-size: 32px;
  font-weight: 700;
  background: purple;
  color: white;
  border: none;
  outline:none;
  margin-left: 15px;
  box-shadow:5px 5px 15px -5px black;
  border-radius: 50%;
  min-width: 40px;
  min-height: 40px;
  transition: all 0.3s ease 0s;
  padding-bottom: 3px;
}
.btn:hover {
  background-color: #2EE59D;
  box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
  color: #fff;
  transform: translateY(-7px);
}
ol{
  margin-top: 30px;
}
ol li{
padding-left: 0;
text-align: left;
min-height: 40px;
font-size: 25px;
font-weight: 500;
display:flex;
align-items: center;
color:rgb(80, 80, 148);
overflow: hidden;
text-transform: capitalize;
}
@media (max-width:768px){
  .box{
    width:50%
  }

}